import React, { useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import IMAGES from "../../assets/images";
import { AiOutlineEye, AiOutlineEyeInvisible } from "react-icons/ai";
import { useForm } from "react-hook-form";
import { FieldError } from "../../components/ErrorMessage/ErrorMessage";
import { Link, useNavigate } from "react-router-dom";
import { PATH } from "../../utils/path";
import { useDispatch, useSelector } from "react-redux";
import { logIn } from "../../app/features/auth/auth.slice";
import { ClipLoader } from "react-spinners";

export default function SignIn() {
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const { isLoading } = useSelector((state) => state.auth);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  function onSubmit(data) {
    const moveToNext = () => {
      navigate(PATH.OTPCODE, { state: data.email });
    };

    const finalData = {
      email: data.email,
      password: data.password,
    };
    dispatch(logIn({ finalData, moveToNext }));
  }
  return (
    <div className="signin-component h-100">
      <Row className="vh-100 m-0">
        <Col sm={12} md={12} lg={7} className="p-0">
          <div className="bg-img">
            <div className="signin-page">
              <div
                style={{
                  padding: "22% 44% 22% 9%",
                }}
              >
                <h4 className="heading1">GP care with a Modern Touch.</h4>
                <p className="heading2">
                  Fast, hassle-free healthcare for the most common medical
                  conditions from the comfort of your home.
                </p>
              </div>
            </div>
          </div>
        </Col>
        <Col
          sm={12}
          md={12}
          lg={5}
          className="d-flex align-items-center py-4 h-100"
        >
          <div className="signin-content">
            <div className="d-flex justify-content-center w-100 my-4">
              <img
                src={IMAGES.LOGO}
                alt="fda-main-logo"
                className="img-fluid"
              />
            </div>
            <Card className="border-0 bg-transparent">
              <Card.Body>
                <Form className="p-3" onSubmit={handleSubmit(onSubmit)}>
                  <div className="signin-heading text-center">Sign In</div>
                  <p className="text-center mb-5">
                    Please enter your details to sign in your account.
                  </p>
                  <Form.Group className="mb-3">
                    <Form.Label className="label-primary">Email</Form.Label>
                    <Form.Control
                      type="email"
                      name="email"
                      placeholder="Email"
                      size="lg"
                      {...register("email", {
                        required: true,
                        pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                      })}
                    />
                    {errors.email && (
                      <FieldError message={"This Field is Required"} />
                    )}
                  </Form.Group>
                  <Form.Group
                    className="position-relative"
                    controlId="formBasicPassword"
                  >
                    <Form.Label className="label-primary">Password</Form.Label>
                    <Form.Control
                      type={showPassword ? "text" : "password"}
                      placeholder="Password"
                      name="password"
                      size="lg"
                      {...register("password", { required: true })}
                    />
                    <div onClick={handleClickShowPassword} className="eye-icon">
                      {showPassword ? (
                        <AiOutlineEye size={18} />
                      ) : (
                        <AiOutlineEyeInvisible size={18} />
                      )}
                    </div>
                  </Form.Group>
                  {errors.password && (
                    <FieldError message={"This Field is Required"} />
                  )}
                  <span className="d-flex flex-wrap justify-content-between align-items-center">
                    <Form.Group className="my-3" controlId="formBasicCheckbox">
                      <Form.Check
                        type="checkbox"
                        label="Remember Me"
                        className="card-internal-text me-2"
                      />
                    </Form.Group>
                    <Link to={PATH.FORGETPASSWORD}>
                      <Form.Text className="cursor-pointer forget-pass">
                        Forget Password?
                      </Form.Text>
                    </Link>
                  </span>
                  <Button
                    className={`w-100 mt-4 ${isLoading && "disabled"}`}
                    type="submit"
                  >
                    {isLoading === true ? (
                      <ClipLoader
                        color="white"
                        size={25}
                        className="d-flex m-auto"
                      />
                    ) : (
                      "Sign In"
                    )}
                  </Button>
                  <span className="d-flex justify-content-center pt-3">
                    <Link to={PATH.SIGNUP} className="text-decoration-none">
                      <Form.Text className="cursor-pointer register-patient">
                        Register as a Patient
                      </Form.Text>
                    </Link>
                  </span>
                </Form>
              </Card.Body>
            </Card>
          </div>
        </Col>
      </Row>
    </div>
  );
}
